'use strict';

/*
* When adding modules to the namespace, ensure that they are added in alphabetical order according to module name.
*/

// MODULES //

import setReadOnly from '@stdlib/utils/define-read-only-property';


// MAIN //

/**
* Top-level namespace.
*
* @namespace ns
*/
var ns = {};

/**
* @name allocUnsafe
* @memberof ns
* @readonly
* @type {Function}
* @see {@link module:@stdlib/buffer/alloc-unsafe}
*/
import allocUnsafe from './../alloc-unsafe';
setReadOnly( ns, 'allocUnsafe', allocUnsafe );

/**
* @name Buffer
* @memberof ns
* @readonly
* @type {Function}
* @see {@link module:@stdlib/buffer/ctor}
*/
import Buffer from './../ctor';
setReadOnly( ns, 'Buffer', Buffer );

/**
* @name array2buffer
* @memberof ns
* @readonly
* @type {Function}
* @see {@link module:@stdlib/buffer/from-arraybuffer}
*/
import array2buffer from './../from-array';
setReadOnly( ns, 'array2buffer', array2buffer );

/**
* @name arraybuffer2buffer
* @memberof ns
* @readonly
* @type {Function}
* @see {@link module:@stdlib/buffer/from-arraybuffer}
*/
import arraybuffer2buffer from './../from-arraybuffer';
setReadOnly( ns, 'arraybuffer2buffer', arraybuffer2buffer );

/**
* @name copyBuffer
* @memberof ns
* @readonly
* @type {Function}
* @see {@link module:@stdlib/buffer/from-buffer}
*/
import copyBuffer from './../from-buffer';
setReadOnly( ns, 'copyBuffer', copyBuffer );

/**
* @name string2buffer
* @memberof ns
* @readonly
* @type {Function}
* @see {@link module:@stdlib/buffer/from-string}
*/
import string2buffer from './../from-string';
setReadOnly( ns, 'string2buffer', string2buffer );

/**
* @name reviveBuffer
* @memberof ns
* @readonly
* @type {Function}
* @see {@link module:@stdlib/buffer/reviver}
*/
import reviveBuffer from './../reviver';
setReadOnly( ns, 'reviveBuffer', reviveBuffer );

/**
* @name buffer2json
* @memberof ns
* @readonly
* @type {Function}
* @see {@link module:@stdlib/buffer/to-json}
*/
import buffer2json from './../to-json';
setReadOnly( ns, 'buffer2json', buffer2json );


// EXPORTS //

export default ns;
